/**  */
export function pathContact(...paths) {
    paths = paths || [];
    if (paths.length == 0)
        return "";
    if (paths.length == 1) {
        return paths[0];
    }
    let str = paths.join("");
    //  /  //shop/test//  /shop/test/
    str = str.replace(/\/+/g, '/');
    return str;
}
